.content-wrapper
	&.login, &.registration
		.card-primary:not(.card-outline) > .card-header
			color: $white
			h1, h2, h3, h4, h5, h6
				font-weight: bold
				float: none
				text-align: center

		.card-body
			+media-breakpoint-down('sm')
				padding: 1rem
				fieldset
					border-left: none !important
					border-right: none !important
					padding: 0px !important