.layout-frontend .wrapper
	& > header,
	& > .content-wrapper
		max-width: $meetMaxWidth
		margin: auto
		padding: 20px 40px
		background-color: transparent
		+media-breakpoint-down('sm')
			padding: 15px 25px

	& > header
		padding-bottom: 0px
	& > .content-wrapper
		padding-top: 0px



	& > footer
		background-color: $primary
		text-align: center
		font-weight: bold
		line-height: 2rem
		p, a
			color: $white
			margin-bottom: 0

		.mailchimp
			line-height: 1.4
			margin: auto
			width: 50%
			min-width: 400px
			max-width: 600px
			padding: 20px 40px
			> p
				font-weight: lighter
			h4
				color: white
			#mc_embed_signup
				margin-top: 10px


.text-mustarmag
	color: $mustarmagSarga
.text-liliom
	color: $liliomLila
.text-szolovesszo
	color: $szolovesszoZold
.text-fugefa
	color: $fugeSarga
.text-cedrus
	color: $cedrusKek



